<template>
  <div class="testTracking">
      <video id="video" v-bind:width="this.video.width" v-bind:height="this.video.height" preload autoplay loop muted></video>
      <canvas id="canvas" v-bind:width="this.video.width" v-bind:height="this.video.height" ></canvas>

    <div class="buttonDiv">
      <button class="btn btn-primary" @click="submit()">点击识别</button>
    </div>
    
    <div class="resultDiv">
      <p1>用户名：{{resultName}}</p1>
      <p1>匹配度：{{resultScore}}</p1>
      <p1>识别状态：{{resultStatus}}</p1> 
    </div>
  </div>
</template>

<script>
  import 'tracking/build/tracking-min.js'
  import 'tracking/build/data/face-min.js'
  import 'tracking/build/data/mouth-min.js'
  import 'tracking/examples/assets/stats.min.js'

  export default {
    name:'testTracking',
    data(){
      return {
        model: {
          image: null,
          group: 'BOSCH',
          name:''
        },
        video:{
          width: 740,
          height: 480
        },
        resultName: "",
        resultScore: "",
        resultStatus: "",
        loading: "",
        submitting: false
      }
    },
    mounted(){
      this.openCamera();
    },
    methods:{
       stopTracking: function (){
          // 停止侦测
          this.trackerTask.stop()
          // 关闭摄像头
          this.trackerTask.closeCamera()
       },
       dataURLtoFile : function (dataurl, filename = "file", type) {
          let arr = dataurl.split(",");
          let bstr = atob(arr[1]);
          !type && (type = arr[0].replace("data:", "").replace(";base64", ""));
          let n = bstr.length,
              u8arr = new Uint8Array(n);
          while (n--) {
              u8arr[n] = bstr.charCodeAt(n);
          }
          return new File([u8arr], filename, {
              type
          });
      },
      openCamera () {

        navigator.mediaDevices.getUserMedia({video: {facingMode: 'user'}}).then(function (stream) {
          document.getElementById('video').srcObject = stream;
        });

        var canvas = document.getElementById('canvas');
        var context = canvas.getContext('2d');
        var ref = this;

        var tracker = new tracking.ObjectTracker('face');
        
        tracker.setInitialScale(4);
        tracker.setStepSize(2);
        tracker.setEdgesDensity(0.1);
        
        console.log("traker initialized")
        
        this.trackerTask = tracking.track('#video', tracker, { camera: true });

        tracker.on('track', function(event) {

          context.clearRect(0, 0, canvas.width, canvas.height);
          
          event.data.forEach(function(rect) {
            // draw rect angle on face and display result
            context.font = '11px Helvetica';
            context.strokeStyle = '#a64ceb';
            context.strokeRect(rect.x, rect.y, rect.width, rect.height);    
            if (!ref.submitting && ref.resultStatus != "success") {
              ref.submitting = true;
              ref.submit()
            } 
          });
        });

      },
      submit () {
          let canvas = document.getElementById('canvas')
          let context = canvas.getContext('2d')
          let video = document.getElementById('video')
          
          // get face image
          context.drawImage(video, 0,0, this.video.width, this.video.height)
          this.model.image = this.dataURLtoFile(canvas.toDataURL(),"image.png");

          const formData = new FormData()
        
          formData.append('Image', this.model.image);
          formData.append('GroupId',this.model.group);

          // post to server
          this.axios.post('http://10.172.10.145:8082/api/Face/Verify', formData)
              .then(res => {
                  // Post a status message saying the upload complete
                  if (res.data.msg == "success") {
                      this.resultName = res.data.data.result[0].user_id;
                      this.resultScore = res.data.data.result[0].score;
                      this.resultStatus = res.data.msg;
                  } else {
                      this.resultScore = "";
                      this.resultName = "";
                      this.resultStatus =  res.data.msg;
                  }

                  this.submitting = false;
              })
      }
    },
    destroyed () {
      this.stopTracking();
    }
   
  }

</script>

<style lang="less" scoped>
.testTracking {
  height: 100vh;
  width: 100%;
  position: relative;
  >* {
    position: absolute;
    left: 0;
    right: 0;
    margin: auto;
  }
  video, canvas {
    top: 0;
  }
  .buttonDiv {
    bottom: 10%;
  }
  .resultDiv{
    bottom:20%
  }
}

</style>






